import React, { useState } from "react";


export default function Main() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [email1, setEmail1] = useState("");
  const [password1, setPassword1] = useState("");
  const [name, setName] = useState("");
  const users = [];

  function validateForm() {
    return email.length > 0 && password.length > 0;
  }
  function handleSubmit1(event){
    users.push({"email":email1,"name":name,"pass":password1});

    document.getElementById("cd-02").style.display="none";
    document.getElementById("cd-01").style.display="block";
    event.preventDefault();
  }
  function handleSubmit(event) {
      var x=0
      if(users.length>0){
      for(var j=0;j<users.length;j++){
        var obj1=users[j]
        if (obj1.email===email && obj1.pass===password){
            x=1
            break
        }
      }
      if(x===1){
          alert("Success")
          document.getElementById("cd-02").style.display="none";
          document.getElementById("cd-01").style.display="none";
          document.getElementById("cd-03").style.display="block";
      }
      else{
        alert("Wrong Credentials")
      }
    }
    
    event.preventDefault();
  }
  function myfun(event){
    document.getElementById("cd-02").style.display="block";
    document.getElementById("cd-01").style.display="none";
    event.preventDefault();
  }
  function logout(event){
    document.getElementById("cd-03").style.display="none";
    document.getElementById("cd-01").style.display="block";
    event.preventDefault();
  }
  return (
    <div class="user">
    <header class="user__header">
        <img src="https://s3-us-west-2.amazonaws.com/s.cdpn.io/3219/logo.svg" alt="" />
        <h1 class="user__title">User Registration</h1>
    </header>
    <container id="cd-01">
    <form class="form">
        <div id="email" class="form__group">
            <input type="email" value={email} placeholder="E-mail" onChange={e => setEmail(e.target.value)}  class="form__input" />
        </div>
        
        <div id="password" class="form__group">
            <input type="password" value={password} placeholder="Password" onChange={e => setPassword(e.target.value)}  class="form__input" />
        </div>
        
        <button class="btn" onClick={handleSubmit} type="submit">Sign-In</button>
        <button class="btn-1" onClick={myfun} >Sign-UP</button>
    </form>
    </container>
    <container id="cd-02">
    <form onSubmit={handleSubmit1} class="form">
    <div id="name" class="form__group">
            <input type="text" value={name} placeholder="UserName"
            onChange={e => setName(e.target.value)}  class="form__input" />
        </div>
        <div id="email1" class="form__group">
            <input type="email" value={email1} placeholder="E-mail" onChange={e => setEmail1(e.target.value)}  class="form__input" />
        </div>
        
        <div id="password1" class="form__group">
            <input type="password" value={password1} placeholder="Password"
            onChange={e => setPassword1(e.target.value)}  class="form__input" />
        </div>
        
        <button class="btn" onClick={validateForm()} type="submit">Sign-In</button>
    </form>
    </container>
    <container id="cd-03">
  <h1>Successfully Logged-In {name}</h1>
    <form class="form">
    <div id="name" class="form__group">
            <input type="text" value={name} placeholder={name}  class="form__input" disabled/>
        </div>
        <div id="email1" class="form__group">
            <input type="email" value={email1} placeholder={email1}  class="form__input" disabled/>
        </div>
        <div id="password1" class="form__group">
            <input type="text" value={password1} placeholder={password1}  class="form__input" disabled/>
        </div>
        <button class="btn-1" onClick={logout} >LogOut</button>
    </form>
    </container>
    
</div>
  );
}
